@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
  }
  .fields {
    flex-direction: column;
  }
  .submit-button {
    text-align: center;
  }
  .section-title {
    padding-left: 15px;
  }
}
@media (max-width: 768px) {
  .modal .modal-content .modal-body .items-slider .slider-item .texts {
    bottom: initial !important;
  }
}
@media (max-width: 991px) {
  header #menu-mobile {
    display: block;
  }
  header #menu-mobile.active {
    left: -24px !important;
  }
}
@media (max-width: 768px) {
  #btn-header-orcamento {
    display: none;
  }
  header .container #div-search {
    left: 0;
    padding: 0 15px;
  }
  header .container .menu-header #secondary-menu-header {
    display: none;
  }
  header .container #menu-mobile {
    position: absolute;
    top: 0;
    left: 0 !important;
    background: #cae098;
    width: 100dvw;
    height: 100dvh;
    padding: 15px 30px;
    overflow: auto;
    transform: translateX(-1200px);
    transition: all ease 0.5s;
  }
  header .container #menu-mobile #menu-menu-principal-1 {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  header .container #menu-mobile #menu-menu-principal-1 li a {
    font-size: 16px;
    color: #006635;
    font-weight: bold;
  }
  header .container #menu-mobile #menu-menu-principal-1 li ul {
    display: block;
    position: relative;
    background: transparent;
    padding: 5px 0;
  }
  header .container #menu-mobile #menu-menu-principal-1 li ul li {
    padding: 0;
  }
  header .container #menu-mobile #menu-menu-principal-1 li ul.sub-menu a {
    font-weight: lighter;
  }
  header .container #menu-mobile #menu-menu-principal-1 li::after {
    color: #cae098;
  }
  header .container #menu-mobile #close-menu-mobile {
    position: fixed;
    top: 15px;
    right: 20px;
    z-index: 101;
  }
  header .container #menu-mobile #close-menu-mobile svg {
    width: 25px;
  }
  header .container #menu-mobile #close-menu-mobile svg path {
    fill: #006635;
  }
  header .container #menu-mobile.active {
    transform: translateX(0);
  }
}
@media (max-width: 768px) {
  #slider-topo .container .slider-text {
    width: 75%;
  }
}
@media (max-width: 768px) {
  #nossos-servicos {
    padding: 50px 0 20px 0;
  }
  #nossos-servicos #slider-nossos-servicos {
    padding-top: 40px;
  }
  #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active {
    width: 100%;
  }
  #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive {
    width: 75%;
  }
  #nossos-servicos .content-servico .subservicos-wrapper {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    grid-auto-columns: unset;
    grid-auto-flow: row;
  }
  #nossos-servicos .content-servico .subservicos-wrapper .subservico-item h3 {
    font-size: 14px;
    line-height: 18px;
  }
  #nossos-servicos .lista-cases {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (max-width: 768px) {
  #sobre-anos-mercados .mercados-de-atuacao .item h3 {
    font-size: 16px;
    line-height: 20px;
  }
  #sobre-noticias {
    padding: 50px 0;
  }
  #sobre-noticias #slider-sobre-noticias .row-items-noticias .item {
    width: 100%;
    height: 350px;
  }
  #sobre-noticias #slider-sobre-noticias .row-items-noticias .item a .content-item figure {
    height: 230px;
  }
}
@media (max-width: 768px) {
  #nossos-valores .dados {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  #nossos-valores .dados > *:nth-child(3) {
    grid-column: 1/-1;
  }
  #nossos-valores .missao,
  #nossos-valores .valores,
  #nossos-valores .visao {
    padding: 20px;
  }
}
@media (max-width: 768px) {
  #nossos-clientes {
    padding: 50px 0;
  }
  #nossos-clientes .row-items-nossos-clientes {
    grid-auto-columns: 30%;
  }
}